	.burger {
		z-index: 9999;
		display: flex;
		justify-content: center;
		align-items: center;
		box-sizing: content-box;
		width: 30px;
		height: 30px;
		padding: 15px;
		background: 0 0;
		border: 0;
		user-select: none;
		margin: -15px;
		cursor: pointer;

		&:before {
			display: none; }
		span {
			position: relative;
			display: inline-block;
			width: 30px;
			height: 2px;
			border-radius: 5px;
			vertical-align: bottom;
			background: #fff;
			transition: background .2s .4s;
			&:before {
				border-radius: 5px;
				transform: none;
				content: "";
				position: absolute;
				left: 0;
				top: 10px;
				width: 100%;
				height: 2px;
				background: #fff;
				transform-origin: 50% 50%;
				transition: top .3s .3s,transform .3s; }
			&:after {
				border-radius: 5px;
				content: "";
				position: absolute;
				left: 0;
				top: -10px;
				width: 100%;
				height: 2px;
				background: #fff;
				transform-origin: 50% 50%;
				transition: top .3s .3s,transform .3s; } } }

	.main-nav--open {

		.b-menu.menu-main {
