		.burger>span {
			background: 0 0; }
		.burger>span::before {
			transform: rotate3d(0,0,1,45deg);
			top: 0;
			transition: top .3s,transform .3s .5s; }
		.burger>span::after {
			transform: rotate3d(0,0,1,-45deg);
			top: 0;
			transition: top .3s,transform .3s .5s; }

		.b-header, .b-slider, .b-slider.slider-main, .b-footer, .b-content, .b-slider.slider-detail, .b-slider.slider-nav, .js__slider-menu {
			transform: translate3d(-250px, 0, 0); }

